<script lang="coffee">var convertValue, dispatch, label, sliderValue, snap, translatedValue;

import {
  onMount,
  createEventDispatcher
} from "svelte";

export var value = void 0;

sliderValue = void 0;

dispatch = new createEventDispatcher;

translatedValue = function(val) {
  switch (val) {
    case 0:
      return "0d";
    case 5:
      return "1d";
    case 20:
      return "7d";
    case 30:
      return "14d";
    case 40:
      return "1m";
    case 55:
      return "3m";
    case 70:
      return "6m";
    case 90:
      return "12m";
    default:
      return "all";
  }
};

label = function(val) {
  if (sliderValue < 3) {
    return "Today";
  } else if (sliderValue >= 3 && sliderValue < 10) {
    return "1 day";
  } else if (sliderValue >= 10 && sliderValue < 25) {
    return "7 days";
  } else if (sliderValue >= 25 && sliderValue < 35) {
    return "14 days";
  } else if (sliderValue >= 35 && sliderValue < 45) {
    return "1 month";
  } else if (sliderValue >= 45 && sliderValue < 60) {
    return "3 months";
  } else if (sliderValue >= 60 && sliderValue < 75) {
    return "6 months";
  } else if (sliderValue >= 75 && sliderValue < 95) {
    return "12 months";
  } else {
    return "All Time";
  }
};

// 12.5  / 3
snap = function() {
  if (sliderValue < 3) {
    sliderValue = 0;
  } else if (sliderValue >= 3 && sliderValue < 10) {
    sliderValue = 5;
  } else if (sliderValue >= 10 && sliderValue < 25) {
    sliderValue = 20;
  } else if (sliderValue >= 25 && sliderValue < 35) {
    sliderValue = 30;
  } else if (sliderValue >= 35 && sliderValue < 45) {
    sliderValue = 40;
  } else if (sliderValue >= 45 && sliderValue < 60) {
    sliderValue = 55;
  } else if (sliderValue >= 60 && sliderValue < 75) {
    sliderValue = 70;
  } else if (sliderValue >= 75 && sliderValue < 95) {
    sliderValue = 90;
  } else {
    sliderValue = 100;
  }
  return dispatch("update", translatedValue(sliderValue));
};

convertValue = function(val) {
  switch (val) {
    case "0d":
      sliderValue = 0;
      break;
    case "1d":
      sliderValue = 5;
      break;
    case "7d":
      sliderValue = 20;
      break;
    case "14d":
      sliderValue = 30;
      break;
    case "1m":
      sliderValue = 40;
      break;
    case "3m":
      sliderValue = 55;
      break;
    case "6m":
      sliderValue = 70;
      break;
    case "12m":
      sliderValue = 90;
      break;
    default:
      sliderValue = 100;
  }
  snap();
  return console.log(`converted ${val}`);
};

onMount(function() {
  return convertValue(value);
});

$: horizonLabel = label(sliderValue);
</script>

<div horizon>

  <div>Time Distance: {horizonLabel}</div>
  <div container>
    <div slider>
    <input bind:value={sliderValue} type="range" id="horizon" name="horizon" min="0" max="100" on:change={snap}>
    </div>
    <div ticks>
      <table>
        <tr>
          <td width="5%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="20%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
        </tr>  

      </table>
      <table style="width: 100%">
        <tr>
          <td width="4%" on:click={convertValue("0d")}>
            0
          </td>
          <td width="15%" on:click={convertValue("1d")}>
            1d
          </td>
          <td width="10%" on:click={convertValue("7d")}>
            7d
          </td>
          <td width="10%" on:click={convertValue("14d")}>
            14d
          </td>
          <td width="15%" on:click={convertValue("1m")}>
            1m
          </td>
          <td width="15%" on:click={convertValue("3m")}>
            3m
          </td>
          <td width="20%" on:click={convertValue("6m")}>
            6m
          </td>
          <td width="10%" on:click={convertValue("12m")}>
            12m      
          </td>
          <td on:click={convertValue("all")}>
            all
          </td>
        </tr>    
      </table>
    </div>
  </div>

</div>
