<script lang="coffeescript">// /lists
// List Index
var destroy, dropHandler, goToItem, inlineInputStyle, itemUrl, refreshList, resourceList, sort, updateRootItem;

import {
  push,
  pop,
  replace,
  location
} from "svelte-spa-router";

import RootItemList from '../../helpers/root_item_list.js';

import PageContent from '../_Common/PageContent.svelte';

import PageHeader from '../_Common/PageHeader.svelte';

import Indent from "../_Common/Indent.svelte";

import DraggableItem from "../Lists/_List.svelte";

import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte";

import Menu from "../_Common/Menus/List.svelte";

import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte';

import ProgressPopover from '../_Common/Popovers/Progress.svelte';

import DueDatePopover from '../_Common/Popovers/DueDate.svelte';

// Local Vars
export var list = [];

export var listId = uuid();

export var debug = false;

// List Model
resourceList = new RootItemList;

refreshList = function(items) {
  return list = items;
};

resourceList.selectArchived(refreshList); // load the list from PouchDB


// jumps
itemUrl = "#/lists/"; // links and redirects

goToItem = function(item) {
  return push(`#/lists/${item.id}`);
};

// CRUD
updateRootItem = function(e) {
  var ecb;
  ecb = function(err) {};
  // refreshList()
  return resourceList.patch(e.detail, refreshList, ecb, null);
};

destroy = function(e) {
  var ecb;
  ecb = function(e) {
    console.log("destroy error");
    return console.log(e);
  };
  return resourceList.destroy(e.detail._id, refreshList, ecb);
};

// UI
sort = function(list) {
  if (list != null) {
    return list.sortBy("index");
  } else {
    return [];
  }
};

// DRAG AND DROP

// archived items can be reorged
dropHandler = function(e) {
  var ecb, refresh, scb;
  refresh = false;
  console.log("drop event, root list");
  console.log(e.detail);
  scb = function(refreshedList) {
    console.log("drop success");
    return refreshList(refreshedList);
  };
  ecb = function(e) {
    console.log("drop error");
    return console.log(e);
  };
  return resourceList.drop(e.detail, scb, ecb);
};

inlineInputStyle = ":hover {font-weight: bold}"; // braces cause problem when inline

$: sortedlist = sort(list);
</script>

<style lang="sass">[type="list-item"] {
  width: 100%;
  max-width: 100%; }
  [type="list-item"] [present] {
    /* display: none */ }
  [type="list-item"] [present="true"] {
    display: flex; }
  [type="list-item"]:hover [present] {
    display: flex; }

header h1 {
  font-size: 36px;
  color: var(--darkest-blue); }

section {
  margin: 0px 56px; }

/*# sourceMappingURL=Index.svelte.css.map */</style>

<svelte:head>
  <title>Radar :: Archive</title>
</svelte:head>



<PageContent>
  <PageHeader>Archived Lists</PageHeader>
  <section>
    {#if list && list.length}
      <div>
        
        {#each sortedlist as item, index}
          <DraggableItem item={item} index={index} {listId} on:drop={dropHandler} on:update={updateRootItem}>
            <div layout="row" type="list-item">
              <Indent level={(item.level || 0).clamp(0,5)}/>
              <div flex="initial" section="progress">
                <!-- ProgressPopover {item} bind:value={item.progress} on:update={updateRootItem} on:open on:close/-->
              </div>

              <div flex ellipsis>
                <InlineInput bind:value={item.name} on:update={updateRootItem( { detail: { id: item._id, name: item.name } })} on:clicked={goToItem(item)} {inlineInputStyle} let:label>
                  <span ellipsis><a href="{itemUrl}{item.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
                </InlineInput>
                {#if debug}
                <div>_ID: #{item._id}</div>
                <div>ID: #{item.id}</div>
                <div>Index: #{item.index}</div>
                <div>Level: #{item.level}</div>
                <div>Parent Id: #{item.parent_id}</div>
                {/if}
              </div>
              <div flex="initial">
                <div layout="row">



                  <!-- due date menu -->
                  <div flex="initial" present={item.due_date != null}>
                    <DueDatePopover {item} value={item.due_date} on:update={updateRootItem} on:open on:close/>
                  </div>

                </div>
              </div>

            </div>

            <div slot="menu">
              <Menu {item} on:select={updateRootItem} on:destroy={destroy}/>

            </div>
          </DraggableItem>
          
        {/each}
      </div>
    {:else}
      <div>No Archived Lists</div>
    {/if}
  </section>
</PageContent>
