  box-sizing: border-box;
  padding: 4px 0px; }
  [type="list-item"] [item-interior] {
    width: 100%;
    max-width: 100%; }
  [type="list-item"] [favorite], [type="list-item"] [menu] {
    width: 28px; }
  [type="list-item"] .slot {
    width: 100%;
    max-width: 100%; }
  [type="list-item"] [show-on-hover] svg path {
    fill: transparent; }
  [type="list-item"] [show-on-hover][is-showing="true"] svg path {
    fill: var(--blue) !important; }
  [type="list-item"] [collapsible] {
    width: 24px; }
