      display: inline; }
  [type="list-item"].over {
    background: #dddddd; }
  [type="list-item"].invalid {
    background: var(--dark-red) !important; }
  [type="list-item"]:hover {
    background: #fcfcfc; }
    [type="list-item"]:hover [favorite] svg, [type="list-item"]:hover [favorite] svg.menu, [type="list-item"]:hover [collapsible] svg, [type="list-item"]:hover [collapsible] svg.menu, [type="list-item"]:hover [draggable-item-menu] svg, [type="list-item"]:hover [draggable-item-menu] svg.menu {
      display: inline; }
    [type="list-item"]:hover [show-on-hover] svg path {
      fill: var(--gray); }
    [type="list-item"]:hover [show-on-hover]:hover svg path {
      fill: var(--blue); }
    [type="list-item"]:hover [handle] svg path {
      fill: var(--light-gray); }
    [type="list-item"]:hover [collapsible] button[icon] {
      padding: 0px !important; }
    [type="list-item"]:hover [collapsible] svg path.fill {
      fill: var(--light-gray); }
    [type="list-item"]:hover [collapsible]:hover svg path {
      fill: var(--blue); }

/*# sourceMappingURL=Index.svelte.css.map */</style>
