  width: calc(100% + 56px);
  max-width: calc(100% + 56px);
  margin-left: -24px; }
  [list-draggable] .slot {
    width: calc(100% - 96px);
    max-width: calc(100% - 96px); }
  [list-draggable] [handle], [list-draggable] [draggable-item-menu] {
    width: 24px; }
  [list-draggable] [indent-menu] {
    width: 24px; }
    [list-draggable] [indent-menu] svg, [list-draggable] [indent-menu] svg.menu {
      display: inline; }
  [list-draggable] [handle]:hover {
    cursor: grab; }
