    background: #dddddd; }
  [list-draggable].invalid {
    background: var(--dark-red) !important; }
  [list-draggable]:hover {
    background: #fcfcfc; }
    [list-draggable]:hover [handle] svg, [list-draggable]:hover [handle] svg.menu, [list-draggable]:hover [indent-menu] svg, [list-draggable]:hover [indent-menu] svg.menu, [list-draggable]:hover [draggable-item-menu] svg, [list-draggable]:hover [draggable-item-menu] svg.menu {
      display: inline; }
    [list-draggable]:hover [handle] svg path {
      fill: var(--light-gray); }
    [list-draggable]:hover [draggable-item-menu] svg path {
      fill: var(--dark-gray); }
    [list-draggable]:hover [draggable-item-menu]:hover svg path {
