    flex: 1;
    box-sizing: border-box;
    max-height: 100%; }
  [list-draggable] > [flex=initial] {
    flex: 0 1 auto; }
  [list-draggable] > [flex=auto] {
    flex: 1 1 auto; }
  [list-draggable] > [flex=none] {
    flex: 0 0 auto; }
  [list-draggable] > [flex=noshrink] {
    flex: 1 0 auto; }
  [list-draggable] > [flex=nogrow] {
    flex: 0 1 auto; }

